___CSS_LOADER_EXPORT___.push([module.id, `/* Базовый контейнер аватара */
.avatar-container {
  display: inline-flex; /* Чтобы занимал место по содержимому */
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  overflow: hidden; /* Скрываем все, что выходит за границы */
  user-select: none;
  background-color: #e0e0e0; /* Фоновый цвет для инициалов и плейсхолдера */
  color: #757575; /* Цвет текста/иконки по умолчанию */
  font-weight: 500;
  margin: 5px; /* Отступ для Storybook */
}

/* Стили для изображения */
.avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Масштабируем изображение, сохраняя пропорции */
  display: block; /* Убираем лишний отступ снизу */
}

/* Стили для инициалов */
.avatar-initials {
  text-align: center;
  line-height: 1; /* Для лучшего выравнивания */
}

/* Стили для плейсхолдера (иконки) */
.avatar-placeholder svg {
  display: block; /* Убираем лишний отступ снизу */
  width: 60%; /* Размер иконки относительно контейнера */
  height: 60%;
  fill: currentColor; /* Используем цвет текста контейнера */
}

/* Размеры */
.avatar--small {
  width: 24px;
  height: 24px;
  font-size: 0.75rem; /* 12px */
}
.avatar--medium {
  width: 40px;
  height: 40px;
  font-size: 1rem; /* 16px */
}
.avatar--large {
  width: 56px;
  height: 56px;
  font-size: 1.25rem; /* 20px */
}

/* Форма */
.avatar--circle {
  border-radius: 50%;
}
.avatar--square {
  border-radius: 4px; /* Небольшое скругление для квадрата */
} `, "",{"version":3,"sources":["webpack://./src/components/Avatar/Avatar.css"],"names":[],"mappings":"AAAA,8BAA8B;AAC9B;EACE,oBAAoB,EAAE,uCAAuC;EAC7D,mBAAmB;EACnB,uBAAuB;EACvB,sBAAsB;EACtB,gBAAgB,EAAE,yCAAyC;EAC3D,iBAAiB;EACjB,yBAAyB,EAAE,8CAA8C;EACzE,cAAc,EAAE,oCAAoC;EACpD,gBAAgB;EAChB,WAAW,EAAE,yBAAyB;AACxC;;AAEA,0BAA0B;AAC1B;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB,EAAE,iDAAiD;EACpE,cAAc,EAAE,gCAAgC;AAClD;;AAEA,wBAAwB;AACxB;EACE,kBAAkB;EAClB,cAAc,EAAE,6BAA6B;AAC/C;;AAEA,oCAAoC;AACpC;EACE,cAAc,EAAE,gCAAgC;EAChD,UAAU,EAAE,0CAA0C;EACtD,WAAW;EACX,kBAAkB,EAAE,sCAAsC;AAC5D;;AAEA,YAAY;AACZ;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB,EAAE,SAAS;AAC/B;AACA;EACE,WAAW;EACX,YAAY;EACZ,eAAe,EAAE,SAAS;AAC5B;AACA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB,EAAE,SAAS;AAC/B;;AAEA,UAAU;AACV;EACE,kBAAkB;AACpB;AACA;EACE,kBAAkB,EAAE,sCAAsC;AAC5D","sourcesContent":["/* Базовый контейнер аватара */\n.avatar-container {\n  display: inline-flex; /* Чтобы занимал место по содержимому */\n  align-items: center;\n  justify-content: center;\n  vertical-align: middle;\n  overflow: hidden; /* Скрываем все, что выходит за границы */\n  user-select: none;\n  background-color: #e0e0e0; /* Фоновый цвет для инициалов и плейсхолдера */\n  color: #757575; /* Цвет текста/иконки по умолчанию */\n  font-weight: 500;\n  margin: 5px; /* Отступ для Storybook */\n}\n\n/* Стили для изображения */\n.avatar-image {\n  width: 100%;\n  height: 100%;\n  object-fit: cover; /* Масштабируем изображение, сохраняя пропорции */\n  display: block; /* Убираем лишний отступ снизу */\n}\n\n/* Стили для инициалов */\n.avatar-initials {\n  text-align: center;\n  line-height: 1; /* Для лучшего выравнивания */\n}\n\n/* Стили для плейсхолдера (иконки) */\n.avatar-placeholder svg {\n  display: block; /* Убираем лишний отступ снизу */\n  width: 60%; /* Размер иконки относительно контейнера */\n  height: 60%;\n  fill: currentColor; /* Используем цвет текста контейнера */\n}\n\n/* Размеры */\n.avatar--small {\n  width: 24px;\n  height: 24px;\n  font-size: 0.75rem; 